.forleavemessage{
background:#1F2235;
min-height:300px;
height:auto;
margin-top:40px;
padding:10px;
box-shadow:0px 5px 0px 0px rgba(0,0,0,0.19);
}
.forleavemessage #messagetite{

}
.forleavemessage #messagetite section{
	width:50%;
	float:right;
	display:flex;
	justify-content:flex-start;
	align-items:center;

}
.forleavemessage #messagetite section img{
	width:60px;
}
.forleavemessage #messagetite section h5{
	color:white;
}

.forleavemessage #messageform{
	
	display:flex;
	flex-direction:column;
	padding:10px;

}
.forleavemessage #messageform section{
	
	
	margin-top:30px;

}
.forleavemessage #messageform section input,textarea{
	padding:10px;
	width:60%;
	border-radius:23px;
	border:1px solid white;
	box-shadow:0px 12px 0px 0px rgba(0,0,0,0.19);
	outline:none;
}
.forleavemessage #messageform section input::placeholder{
color:#1F2235;
text-indent:30px;
}
.forleavemessage #messageform section textarea::placeholder{
	color:#1F2235;
	margin-top:10px;
}
.forleavemessage #messageform section textarea{
	border-radius:10px;
	text-indent:30px;
}
.forleavemessage #messageform  button{
	width:150px;
	padding:5px;
	border-radius:33px;
	background:#FF4B57;
	border:1px solid transparent;
	display:flex;
	justify-content:space-around;
	margin-left:30%;
	box-shadow:0px 8px 0px 0px rgba(0,0,0,0.19);
	outline:none;
}
.forleavemessage #messageform  button span{
	color:white;
	font-size:20px;
}
@media (max-width:900px){
	.forleavemessage #messagetite section{
		float:left;
		width:100%;
		
	}
	.forleavemessage #messageform section{
		
	}
	.forleavemessage #messageform section input,textarea{
		margin-left:35%;
	}
	.forleavemessage #messageform  button{
		margin-left:55%;
	}
}
@media (max-width:470px){
	.forleavemessage #messagetite section{
		float:left;
		width:250px;
	}
	.forleavemessage #messageform section{
		
	}
	.forleavemessage #messageform section input,textarea{
		margin-left:15%;
	}
	.forleavemessage #messageform  button{
		margin-left:25%;
	}
}