.forhistoryheader{
 background:#202334;
 min-height:320px;
 height:auto;
 box-shadow:0px 8px 0px 0px rgba(0,0,0,0.19);
 display:flex;
 justify-content:center;

}
.forhistoryheader img{
	height:317px;
	padding:5px;
	margin-left:38%;
	opacity:0.3;
	z-index:-500;
}
.forhistoryheader h4{
	color:white;
	z-index:3000;
	transform:translate(-15%,0%);
	margin-top:-13rem;
	text-transform:none;
	font-size:25px;
	letter-spacing:3px;
	word-wrap:flex;
	word-spacing:3px;
	text-indent:10px;

}
.forhistoryheader h5{
	color:white;
	text-align:center;
	margin-top:60px;
	margin-left:-70px;
}
.forhistorybody{
 background:#D8D8D8;
 min-height:500px;
 height:auto;
 padding:10px;
}
.forhistorybody .contenttitle{
	
	height:auto;
	padding:30px;
	display:flex;
	justify-content:center;
}
.forhistorybody .contenttitle p{
	text-indent:1rem;
	line-height:30px;
	font-size:20px;
	font-family: 'Bebas Neue', cursive;
	
}
.forhistorybody .historycontentholder{
	
	width:80%;
	margin:0px auto;
}
.forhistorybody .historycontentholder .formediaaslist{
	min-height:500px;
	list-style:none;
	padding:10px;
	display:flex;
	flex-direction:column;
}
.forhistorybody .historycontentholder .formediaaslist li img{
  width:150px;
  height:150px;
  border:5px solid white;
  box-shadow:0px 7px 0px 0px rgba(0,0,0,0.19);
  float:left;
  z-index:3000px
}
.forhistorybody .historycontentholder .formediaaslist li .sectionofcontenttext{
margin-left:12rem;
padding:10px;

}

.forhistorybody  .historycontentholder .formediaaslist .sectionofcontenttext p {
	text-align:center;
	font-size:18px;
	line-height:45px;
	font-family: 'Josefin Sans', sans-serif;
	font-weight:400;
	text-transform:initial;
	
}
.forhistorybody .historycontentholder .formediaaslist li:last-child:before{
display:none;
}
.forhistorybody .historycontentholder .formediaaslist li:before{
	content:"";
	position:absolute;
	background:grey;
	border-bottom:5px solid grey; 
	width:3px;
	min-height:320px;
	height:auto;
	margin-top:10.4rem;
	margin-left:4rem;
	z-index:-1000px;
	opacity:0.3;
	display:none;
}
.forhistorybody  .forlinktoAuthorholder{

	height:100px;
	margin-top:10px;
	display:flex;
	flex-direction:column;
	align-items:center;
	justify-content:center;
}
.forhistorybody  .forlinktoAuthorholder button{
	width:250px;
	padding:10px;
	border-radius:33px;
	border:1px solid transparent;
	box-shadow:0px 10px 0px 0px rgba(0,0,0,0.19);
	color:white;
	background:#FF4B57;
	color:#202334;
}
.forhistorybody  .forlinktoAuthorholder small{
	margin-top:10px;
}
@media (max-width:1100px){

.forhistorybody .historycontentholder .formediaaslist li:before{
		display:none;
	}

}
@media (max-width:900px)
{
	.forhistoryheader h4{
		text-align:center;
		font-size:25px;
		text-indent:10rem;
	}
	.forhistoryheader h5{
		margin-left:2px;
	}
	.forhistorybody .historycontentholder .formediaaslist li:before{
		display:none;
	}
}
@media (max-width:600px){
	.forhistoryheader img{
		margin-left:5rem;

	}
	.forhistoryheader h4{
		text-align:center;
		font-size:18px;
		text-indent:5rem;
	}
	.forhistoryheader h5{
		margin-left:-9rem;
	}
	.forhistorybody .historycontentholder .formediaaslist li .sectionofcontenttext{
		float:left;
		width:100%;
		margin-left:-2rem;
		padding:2px;
		margin-top:10px;
	}
    .forhistorybody .historycontentholder .formediaaslist li img{

      width:330px;
      height:330px;
      border:10px solid white;
    }
}
@media (max-width:500px){
	.forhistoryheader img{
		margin-left:5rem;

	}
	.forhistoryheader h4{
		text-align:center;
		font-size:18px;
		text-indent:4rem;
	}
	.forhistorybody .historycontentholder .formediaaslist li img{

      width:330px;
      height:330px;
      border:10px solid white;
      margin-left:-3rem;
    }

}
