.forfooter{
	background:#1F2235;
	min-height:70px;
	border-top:1px solid grey;
	color:white;
	font-size:14px;
}
.forfooter .footerpart1{
	border:0px solid grey;
	display:flex;
	flex-direction:column;
	justify-content:flex-end;
}
.forfooter .footerpart1 span{
	color:#28DEB2;
}
.forfooter .footerpart2{
	border:0px solid grey;
}
.forfooter .footerpart2 span{
	color:#28DEB2;
}
.forfooter .footerpart3{
	border:0px solid grey;
}
.forfooter .footerpart3 img{
	float:right;
}
@media (max-width:600px){
	.forfooter .footerpart1{
	border-top:1px solid grey;
	box-shadow:0px 2px 0px 1px rgba(0,0,0,0.19);
	border-bottom:1px solid grey;
}
.forfooter .footerpart2{
	padding:10px;
	box-shadow:0px 2px 0px 1px rgba(0,0,0,0.19);
	border-bottom:1px solid grey;
}
}
@media (max-width:900px){
	.forfooter .footerpart1{
	border-top:1px solid grey;
	box-shadow:0px 2px 0px 1px rgba(0,0,0,0.19);
	border-bottom:1px solid grey;
}
.forfooter .footerpart2{
	padding:10px;
	box-shadow:0px 2px 0px 1px rgba(0,0,0,0.19);
	
}
}
