.fornav{
width:100%;
height:50px;
box-shadow:0px 0px 5px 0px rgba(0,0,0,0.19);
position:fixed;
top:0;
background-color:#202334;
display:flex;
justify-content:space-around;
align-items:center;
color:white;
z-index:2000;
}
.forlogo{
	height:49px;
	background:red
}
.formenus{
	font-size:13px;
	font-family:"Times New Romans";
	height:49px;
	width:50%;
	margin-top:18PX;

}
.formenus ul{
	display:flex;
	justify-content:space-between;
	list-style:none;
}

.link{
	text-decoration:none;
	color:white
}
.link:hover,.link:active{
	color:#FF4B57;
}
.linkactive{
	color:#FF4B57;
	border-bottom:3px solid #FF4B57;
}
/*togglebutton styles*/
.buttontoggle{
 background:white;
 color:black;
 border:1px solid white;
 box-shadow:0px 0px 2px 0px rgba(0,0,0,0.19);
 outline:none;
 display:flex;
 margin-right:10px;
}
.buttontoggle span{
	margin-top:4px;
}
.buttontoggle:hover{
	cursor:pointer;
	outline:none;
	background-color:#202334;
	box-shadow:0px 0px 2px 0px rgba(0,0,0,0.19);
	color:white;
	outline:none;
}
/*div hold menu for responsive styles*/
.forresponsivemenu{
width:0%;
height:100%;
background:#202334;
box-shadow:0px 0px 10px 0px rgba(0,0,0,0.19);
z-index:300;
position:absolute;
position:fixed;
left:0;
top:0;
overflow-x:hidden;
transition:width .2s ease-in-out;
display:flex;
flex-direction:column;
justify-content:flex-start;
color:white;
}
.forresponsivemenu.animatewidth{
	width:80%;
}
.forbackresponsive{
width:0%;
height:100%;
background:black;
opacity:0.7;
box-shadow:0px 0px 10px 0px rgba(0,0,0,0.19);
position:absolute;
position:fixed;
z-index:200;
top:0;
left:0;	
color:white;
}
.forbackresponsive.fullwidth{
	width:100%;
}
.forresponsivemenu .resforlog{
	margin-top:15px;
}
.forresponsivemenu .resformenus{
	margin-top:10px;
	min-height:500px;
	height:auto;
	width:100%;
}

.resformenus ul{
	display:flex;
	flex-direction:column;
	justify-content:flex-start;
	list-style:none;

	align-items:center;
}
.resformenus ul li{
	margin-top:50px;
	margin-left:-50px;
}
.reslink{
	text-decoration:none;
	color:white;
	margin-left:-10px;
	font-size:18px;
	cursor:pointer;
}
.reslink:hover,.reslink:active{
	color:#FF4B57;
	cursor:pointer;
}
@media (min-width:901px)
{
	.buttontoggle{
		display:none;
	}
}
@media (max-width:900px)
{
	.formenus ul{
		display:none;
	}
}

