.forabout {
background:#D8D8D8;
min-height:500px;
height:auto;
}
.forabout .fortiltle{

  padding:10px;
  margin-top:40px;
  display:flex;
  align-items:center;
  justify-content:center;
}
.forabout .fortiltle h3{
  color:#202334;
}
.forabout .forimageandnames{
    padding:10px;
  margin-top:40px;
  display:flex;
  align-items:center;
  justify-content:center;
  flex-direction:column;

}
.forabout .forimageandnames img{
  border-radius:50%;
  width:170px;
  height:170px;
  border:5px solid white;
  box-shadow:0px 7px 0px 0px rgba(0,0,0,0.19); 
}
.forabout .forimageandnames h4{
  text-transform:none;
  margin-top:15px;
  font-weight:bold;
}
.forabout .foraboutcards{
  box-shadow:0px 0px 0px 0px rgba(0,0,0,0.19);
  margin-top:50px;
  min-height:300px;
  display:grid;
  grid-template-columns:repeat(auto-fit,minmax(200px,300px));
  justify-content:space-around;
  background:transparent;
  

}
.forabout .foraboutcards div{
  box-shadow:0px 10px 22px 0px rgba(0,0,0,0.19);
  background:#202334;
  height:380px;
  display:flex;
  flex-direction:column;
  margin-top:5px;
  margin-bottom:10px;
transform:scale(0.9);
transition:transform .2s ease-in-out;
cursor:pointer;
}
.forabout .foraboutcards div #cardheader{
  background:white;
  padding:10px;
  background:transparent;
  box-shadow:0px 7px 6px 0px rgba(0,0,0,0.19);
  border-bottom:1px solid grey;
  color:white;
  text-align:center;
}
.forabout .foraboutcards div #cardbody{
  padding:10px;
  margin-top:10px;
  height:250px;
}
.forabout .foraboutcards div #cardbody h5{
  color:#6B86F5;
}
.forabout .foraboutcards div #cardbody h5 span{
  color:white;
}
.forabout .foraboutcards div #cardbody p{
  color:white;
  line-height:32px;
}
.forabout .foraboutcards div #cardfooter{

height:66px;
padding:10px;
}
.forabout .foraboutcards div #cardfooter button{
  width:150px;
  padding:5px;
  border-radius:23px;
  box-shadow:0px 7px 0px 0px rgba(0,0,0,0.19);
  background:#FF4B57;
  border:1px solid #FF4B57;
  color:white;
  font-size:18px;
}
.forabout .foraboutcards div:hover{
  transform:scale(1);
}

@media (max-width:900px){

.forabout .foraboutcards div{
  box-shadow:0px 10px 22px 0px rgba(0,0,0,0.19);
  background:#202334;
  height:380px;
  display:flex;
  flex-direction:column;
  margin-top:35px;

}
}
