.quotes{
  background:#F5F5F5;
  padding:5;
  width:100%;
  min-height:615px;
  height:auto;
  color:black;
  display:block;
  justify-content:space-between;
  align-items:center;
  box-sizing: border-box;

}
.quotes label{
  color:#202334;
 margin-left:20px;
 margin-top:20px;
}
.quotes .quotesgallery{
  width:80%;
  min-height:450px;
  height:auto;
  box-shadow:0px 0px 0px 0px rgba(0,0,0,0.19);
  margin-top:-10px;
  margin:5rem auto;
  display:grid;
  grid-column-gap:28px;
  padding:10px;
  margin:0px auto;
  grid-template-columns:repeat(auto-fit,minmax(300px,300px));  
}
.quotes .quotesgallery section{
  padding:10px;
}
.quotesgallery img{
  border:10px solid white;
  box-shadow:0px 0px 15px 0px rgba(0,0,0,0.19);
  width:auto;
  transform: scale(0.9);
  transition:transform .2s ease-in-out;
  height:auto;
}
.quotesgallery img:hover{
   box-shadow:0px 0px 50px 0px rgba(0,0,0,0.19);
transform:scale(1.2);
z-index:100;
 margin:0px auto;
 position:relative;
 margin:0px auto;
 top:0;
 cursor:pointer;
}
