.quotes{
  background:#EEF5F6;
  padding:5;
  width:100%;
  min-height:615px;
  height:auto;
  color:black;
  display:block;
  justify-content:space-between;
  align-items:center;
  box-sizing: border-box;

}
.quotes label{
  color:#0B79E8;
 margin-left:20px;
}
.quotes .quotesholder{
  width:90%;
  min-height:450px;
  height:auto;
  box-shadow:0px 0px 0px 0px rgba(0,0,0,0.19);
  margin-top:-10px;
  margin:5rem auto;
  display:grid;
  grid-template-columns:repeat(auto-fit,minmax(250px,300px));
  justify-content:space-around;
  grid-column-gap:30px;
  grid-row-gap:15px;



}
.quotes .cardquote{
  box-shadow:0px 0px 10px 0px rgba(0,0,0,0.19);
  height:270px;
  border-radius:5px;
 background:#f5f5f5;
 display:grid;
 grid-template-rows:80% 20%;
 cursor:pointer;

}
.quotes .cardquote .quotescontent{
  box-shadow:0px 2px 9px 0px rgba(0,0,0,0.19);
  background:white;
  padding:20px;
 font-size:18px;
 font-family:"Times New Roman";
 box-sizing:content-box;
 height:auto;
}
.sharequotes{
  display:flex;
  justify-content:space-around;
  align-items:center;
}
.qIcon{
  padding:10px;
  font-size:40px;
  cursor:pointer;
}
.qIcon:hover{
  box-shadow:0px 0px 10px 0px rgba(0,0,0,0.19);
  transition:2s;
  color:#0B79E8;
}
