
.forheader{
  background:#202334;
  padding-right:0;
  padding-left:0;
  min-height:370px;
  height:auto;
  box-shadow:0px 5px 0px 0px rgba(0,0,0,0.19);
  margin-top:25px;
}
.forheaderText{
border-right:0px solid grey;
align-items:center;
}
.forheaderText .fortexts{
  color:white;
 
  width:100%;
  min-height:200px;
  height:auto;
  transform:translate(0%,50%);

}
.forheaderText .fortexts h2,h4,h6{
  text-align:center;
  text-transform:uppercase;
  line-height:30px;
  font-family: 'Bebas Neue', cursive;
}
.forheaderText .fortexts h2{
  letter-spacing:4px;
  word-spacing:6px;
}
.forheaderText .fortexts h4{
  font-size:23px;
  margin-left:-5px;
  letter-spacing:2px;
  word-spacing:6px;
}
.forheaderText .fortexts h6{
  margin-left:15px;
   font-size:16px;
   letter-spacing:2px;
   word-spacing:4px;
  }

.forheaderText .fortexts .findmorelink{
border-bottom:none;
list-style:none;
color:red;
text-decoration:none;
}

.forheaderText .fortexts button{
  background:#FF4B57;
  border-radius:33px;
  border:1px solid #FF4B57;
  padding:10px;
  width:240px;
  color:white;
  margin-left:35%;
  font-size:22px;
  display:flex;
justify-content:space-around;
box-shadow:0px 12px 0px 0px rgba(0,0,0,0.19);
outline:none;
font-family:"Cambria";
text-decoration:none;
}


.forheaderImag{
 
}
.forheaderImage img{
  width:267px;
  margin-top:-10px;
}
@media (max-width:600px){
  .forheaderText{
  }
  .forheaderText .fortexts{
    transform:translate(0%,10%);
  }
  .forheaderText .fortexts button{
    margin-left:17%;
  }
}
@media (max-width:500px){
  .forheaderText{
  }
  .forheaderText .fortexts{
    transform:translate(0%,10%);
  }
  .forheaderText .fortexts button{
    margin-left:12%;
    
  }
}
@media (max-width:1100px){
 .forheaderText .fortexts button{
    margin-left:31%;
    margin-top:-30px;
  }
  .forheaderImage img{
    margin-top:-5px;
  }
  }